<template>
  <div id="message" class="messages" v-bind:class="{ 'right': msg.origin===1, 'left': msg.origin!==1}">
    <div class="img-and-bubble">
      <img v-if="msg.origin===1" alt="Me" v-bind:src="user.avatar_image"/>
      <img v-else-if="msg.origin===0" alt="Me" v-bind:src="chatroom.portrait"/>
      <img v-else-if="msg.origin.portrait" v-bind:alt="msg.origin.username" v-bind:src="msg.origin.portrait"/>
      <div :id="'badge_menu'+msg.identifier"
           v-bind:class="{ 'mdl-menu--bottom-right': msg.origin===1, 'mdl-menu--bottom-left': msg.origin!==1}">
        <div class="bubble">
          {{msg.message}}
        </div>
        <div v-if="msg.origin===1" class="link mdl-badge mdl-badge--overlap"
             v-bind:class="{ 'is-it-a-question': !msg.question,
                             'is-it-a-question-blink': couldBeAQuestion(msg)}"
             v-bind:data-badge="badge_icon">
        </div>
        <ul v-if="!msg.origin.portrait" class="mdl-menu mdl-js-menu mdl-js-ripple-effect"
            v-bind:class="{ 'mdl-menu--bottom-right': msg.origin===1, 'mdl-menu--bottom-left': msg.origin!==1}"
            :for="'badge_menu'+msg.identifier" :data-mdl-for="'badge_menu'+msg.identifier">
          <li v-for="action in badge_menu_actions" v-on:click="doAction(action.js, msg, action)"
              class="mdl-menu__item" v-bind:class="{ 'mdl-menu__item--full-bleed-divider': action.separatorAfter}">
            <span v-if="action.post" :title="action.post.body">{{$t('badge_menu.action.' + action.labelId)}} {{action.post.id}}</span>
            <span v-else>{{$t('badge_menu.action.' + action.labelId)}}</span>
          </li>
        </ul>
      </div>
    </div>
    <div v-if="!msg.origin.portrait" class="time">
      {{msg.date.toLocaleTimeString()}}
    </div>
    <div v-else class="time">{{msg.date.toLocaleTimeString() + ' - ' + msg.origin.username}}</div>
  </div>
</template>

<script>
  import {authMixin} from '@/auth/authMixin.js'
  import axios from 'axios'

  require('material-design-lite')

  export default {
    name: 'msg-item',
    mixins: [authMixin],
    props: ['msg', 'user', 'chatroom', 'questions'],
    data: function () {
      return {update_version: 0}
    },
    computed: {
      badge_icon: function () {
        if (this.msg.question) {
          return this.msg.post_id || '...'
        } else {
          if (this.msg.message.endsWith('?')) {
            return '?'
          }
          return '⋮'
        }
      },
      type: function () {
        if (this.msg.question) {
          return 'question'
        }
        if (this.msg.answer) {
          return 'answered_question'
        }
        if (this.msg.question) {
          return 'answer'
        }
        return 'message'
      },
      badge_menu_actions: function () {
        let vm = this
        let entries = [{
          'js': 'editMsg',
          'labelId': 'editMsg'
        }, {
          'js': 'changeQuestion',
          'labelId': 'isAQuestion',
          'separatorAfter': true
        }
        ]
        if (vm.questions instanceof Array && vm.questions.length > 0) {
          let qEntries = []
          for (const q of vm.questions) {
            if (q.answer_to === null) {
              qEntries.push({
                'js': 'AnswerTo',
                'labelId': 'AnswerTo',
                'post': q
              })
            }
          }
          qEntries.sort((a, b) => (a.post.id > b.post.id) ? 1 : (b.post.id > a.post.id) ? -1 : 0)
          for (let action in qEntries) {
            entries.push(qEntries[action])
          }
        }
        return entries
      }
    },
    methods: {
      updatemdl: function () {
        // eslint-disable-next-line
        componentHandler.upgradeDom()
        // eslint-disable-next-line
        componentHandler.upgradeAllRegistered()
      },
      couldBeAQuestion: function (msg) {
        return !!(!msg.question && msg.message.endsWith('?'))
      },
      doAction: function (actionName, msg, action) {
        let vm = this
        if (actionName === 'changeQuestion') {
          vm.updateQuestion(msg, action)
        } else if (actionName === 'AnswerTo') {
          console.log(actionName, msg, action.post)
          vm.setAnswer(msg, action.post)
        }
      },
      updateQuestion: function (msg) {
        let vm = this
        msg.question = !msg.question
        if (msg.question === true || msg.post_id) {
          msg.room = vm.$route.params.id
          axios.post('/api/chatroomquestion/', msg, vm.authHeader())
            .then(function (response) {
              // handle success
              vm.$root.loading = false
              if (response.data.post) {
                vm.$set(msg, 'post_id', response.data.post.post_id)
                if (msg.question) {
                  vm.$root.showSnackbar(vm.$i18n.t('post.savedAsQuestion'))
                } else {
                  vm.$root.showSnackbar(vm.$i18n.t('post.notAQuestionAnymore'))
                }
                console.log(response.data.questions)
                if (response.data.questions && vm.$root.questions instanceof Object) {
                  vm.$set(vm.$root.questions, vm.$route.params.id, response.data.questions)
                }
              } else {
                vm.errors = []
                vm.errors.push({message: response.data.message})
              }
            })
            .catch(function (error) {
              // handle error
              console.log(error)
              vm.$root.loading = false
              vm.errors = []
              vm.errors.push(error)
            })
            .then(function () {
              // always executed
              vm.$root.loading = false
              vm.$nextTick(vm.updatemdl())
            })
        }
      },
      setAnswer: function (answer, question) {
        let vm = this
        answer.room = vm.$route.params.id
        let QandA = {
          question: question,
          answer: answer,
          room: vm.$route.params.id
        }
        console.log(QandA)
        axios.post('/api/chatroomsetanswer/', QandA, vm.authHeader())
          .then(function (response) {
            // handle success
            vm.$root.loading = false
            console.log(response.data)
            if (response.data.question && response.data.answer) {
              vm.$set(answer, 'id', response.data.answer.id)
              vm.$set(answer, 'answer_to', response.data.answer.answer_to)
              vm.$set(answer, 'type', response.data.answer.type)
              if (response.data.question.answer) {
                vm.$root.showSnackbar(vm.$i18n.t('post.questionAnswered'))
              } else {
                vm.$root.showSnackbar(vm.$i18n.t('post.answerRejected'))
              }
              if (response.data.questions && vm.$root.questions instanceof Object) {
                vm.$set(vm.$root.questions, vm.$route.params.id, response.data.questions)
              }
            } else {
              vm.errors = []
              vm.errors.push({message: response.data.message})
            }
          })
          .catch(function (error) {
            // handle error
            console.log(error)
            vm.$root.loading = false
            vm.errors = []
            vm.errors.push(error)
          })
          .then(function () {
            // always executed
            vm.$root.loading = false
            vm.$nextTick(vm.updatemdl())
          })
      }
    },
    mounted: function () {
      let vm = this
      vm.updatemdl()
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .is-it-a-question:after {
    color: #ffffff;
    background-color: #4CAF50;
  }

  .is-it-a-question-blink:after {
    color: #ffffff;
    background-color: #4CAF50;
    animation: colorRotate 5s infinite;
    -moz-animation: colorRotate 5s infinite; /* Firefox */
    -webkit-animation: colorRotate 5s infinite; /* Safari and Chrome */
  }

  @-moz-keyframes colorRotate /* Firefox */
  {
    0% {
      background: #4CAF50;
      filter: hue-rotate(0deg);
    }
    50% {
      background: rgb(255, 64, 129);
      filter: hue-rotate(360deg);
    }
    100% {
      background: #4CAF50;
      filter: hue-rotate(0deg);
    }
  }

  @-webkit-keyframes colorRotate /* Safari and Chrome */
  {
    0% {
      background: #4CAF50;
      -webkit-filter: hue-rotate(0deg);
    }
    50% {
      background: rgb(255, 64, 129);
      -webkit-filter: hue-rotate(360deg);
    }
    100% {
      background: #4CAF50;
      -webkit-filter: hue-rotate(0deg);
    }
  }

  @keyframes colorRotate /* Safari and Chrome */
  {
    0% {
      background: #4CAF50;
      -webkit-filter: hue-rotate(0deg);
    }
    50% {
      background: rgb(255, 64, 129);
      -webkit-filter: hue-rotate(360deg);
    }
    100% {
      background: #4CAF50;
      -webkit-filter: hue-rotate(0deg);
    }
  }

  #message {
    display: flex;
    flex-direction: column;
    padding: 0 0 5px 58px;
    clear: both;
    text-align: left;
    max-width: 60%;
    margin-top: 1vh;
  }

  #message.right {
    float: right;
    padding: 0 58px 5px 0;
    margin-right: -19px;
    margin-left: 19px;
  }

  #message div.img-and-bubble {
    position: relative;
  }

  #message img {
    position: absolute;
    bottom: 0;
    left: -38px;
    border-radius: 50%;
    width: 30px;
    margin-top: 12px;
  }

  #message.right img {
    right: -38px;
    left: auto;
  }

  #message .bubble {
    position: relative;
    float: left;
    background: #dbe3f9;
    font-size: 13px;
    font-weight: 600;
    padding: 8px 8px;
    border-radius: 5px 5px 5px 0px;
    color: #365dad;
    width: max-content;
    word-break: break-word;
    max-width: 100%; /* do not oversize message width*/
  }

  #message.right .bubble {
    float: right;
    border-radius: 5px 5px 0px 5px;
  }

  #message .bubble:after {
    content: '';
    position: absolute;
    top: 100%;
    width: 0;
    height: 0;
    border: 7px solid transparent;
    border-bottom: 0;
    margin-top: -7px;

  }

  #message.left .bubble:after {
    left: 0;
    border-right-color: #dbe3f9;
    border-left: 0;
    margin-left: -7px;
  }

  #message.right .bubble:after {
    right: 0;
    border-left-color: #dbe3f9;
    border-right: 0;
    margin-right: -7px;
  }

  #message > .time {
    clear: both;
    color: #8eace8;
    font-size: 0.8em;
    position: relative;
    bottom: 0;
    margin-bottom: 0.5em;
    width: max-content;
  }

  #message.left > .time {
    margin-left: 0;
    margin-right: auto;
  }

  #message.right > .time {
    margin-left: auto;
    margin-right: 0;
  }
</style>



